import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import { Button } from './Button';
import './Navbar.css';


const Navbar = () => {
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);

    const showButton = () => {
        if(window.innerWidth <=960){
            setButton(false);
        }else{
            setButton(true);
        }
    };

    useEffect(() => {
        showButton();
    }, []);

    window.addEventListener('resize', showButton);

    return (
        <>
           <nav className="navbar">
               <div className="navbar-container">
                   <Link to="/cue-ilisound" className="navbar-logo" onClick={closeMobileMenu}>
                       <i className="fab fa-cuttlefish"/>UE ilisound 
                   </Link>
                   <div className="menu-icon" onClick={handleClick}>
                       <i className={click ? 'fas fa-times' : 'fas fa-bars'}/>
                   </div>
                   <ul className={click ? "nav-menu active" : "nav-menu"}>
                       <li className='nav-item'>
                           <Link to ='/cue-ilisound' className="nav-links" onClick={closeMobileMenu}>
                               Inicio
                            </Link>
                       </li>
                       <li className='nav-item'>
                           <Link to ='/paquetes' className="nav-links" onClick={closeMobileMenu}>
                               Paquetes
                            </Link>
                       </li>
                       <li className='nav-item'>
                           <Link to ='/aboutus' className="nav-links" onClick={closeMobileMenu}>
                               Quienes somos
                            </Link>
                       </li>
                   </ul>
                   {button && <Button
                   buttonStyle="btn--outline">CONTACTANOS</Button>}
               </div>

            </nav> 
        </>
    )
}

export default Navbar
